@charset "UTF-8";

/* Light theme color */
$light-background: #0f0700;
$light-background-secondary: #0f0700;
$light-background-header: #0f0700;
$light-color: aliceblue;
$light-color-variant: white;
$light-color-secondary: #b3b3bd;
$light-border-color: #4e4e57;
$light-table-color: #4e4e57;

/* Dark theme colors */
$dark-background: #0f0700;
$dark-background-secondary: #0f0700;
$dark-background-header: #0f0700;
$dark-color: aliceblue;
$dark-color-variant: white;
$dark-color-secondary: #b3b3bd;
$dark-border-color: #4e4e57;
$dark-table-color: #4e4e57;

$media-size-phone: "(max-width: 684px)";
$media-size-tablet: "(max-width: 900px)";

/* Variables for js, must be the same as these in @custom-media queries */
:root {
  --phoneWidth: (max-width: 684px);
  --tabletWidth: (max-width: 900px);
}

/* Content */
$max-width: 860px;