/* ==== POST CONTAINER ==== */
.post {
  width: unquote("min(100%, 1200px)");
  margin: 2rem auto;
  padding: unquote("clamp(1rem, 2vw, 2rem)");
  color: #E6E3DC;
  background-color: #3A3A3A;
  background-image:
    linear-gradient(rgba(0,0,0,0.25), rgba(0,0,0,0.25)),
    url("https://www.transparenttextures.com/patterns/paper-fibers.png");
  background-size: unquote("clamp(150px, 25vw, 300px)");
  background-blend-mode: overlay;
  border-radius: 12px;
  line-height: 1.6;
  font-family: 'VT323', monospace;
  box-sizing: border-box;
  box-shadow: 0 0 24px rgba(0,0,0,0.4);
}

/* ==== HEADINGS ==== */
.post-title {
  font-size: unquote("clamp(1.6rem, 4.2vw, 3rem)");
  margin-bottom: 1rem;
  font-weight: 700;
  color: #F5F5F5;
}

.post-title a {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s;
}

/* ==== DATE & TAGS ==== */
.post-date {
  font-size: unquote("clamp(0.7rem, 0.9vw, 0.8rem)");
  opacity: 0.7;
  color: #5A564F;
  margin-bottom: 1.25rem;
}

.post-date:after {
  content: " —";
}

.post-tags {
  display: inline-block;
  font-size: unquote("clamp(0.7rem, 0.9vw, 0.8rem)");
  opacity: 0.6;
  margin-bottom: 1.5rem;
}

.post-tags a {
  text-decoration: none;
  margin-right: 0.5rem;
  color: #5A564F;
}

.post-tags a:hover {
  color: #1C1B19;
}

/* ==== CONTENT ==== */
.post-content {
  margin-top: 2rem;
}

.post-content p {
  margin-bottom: 1.25rem;
  color: darkgrey;
  font-family: 'VT323', monospace;
  font-size: unquote("clamp(0.9rem, 1.7vw, 1.7rem)");
}

.post-content h3 {
  font-size: unquote("clamp(1.3rem, 3.5vw, 2.6rem)");
  color: #DADADA;
}

.post-content p code,
.post-content h3 code {
  font-family: 'VT323', monospace;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: unquote("clamp(0.9rem, 1.7vw, 1.7rem)");
  background: rgba(255,255,255,0.1);
}

.post-content pre {
  padding: unquote("clamp(0.4rem, 1.2vw, 0.9rem)");
  border-radius: 8px;
  overflow-x: auto;
  margin-bottom: 2rem;
  color: #2C2A28;
  font-size: unquote("clamp(0.7rem, 0.9vw, 0.9rem)");
}

/* ==== EXCERPT ==== */
.post-excerpt {
  color: #5A564F;
}

/* ==== PAGINATION ==== */
.pagination__title-h {
  position: relative;
  background: #D6CFC0;
  padding: 0 1rem;
  font-size: unquote("clamp(0.6rem, 0.9vw, 0.8rem)");
  text-transform: uppercase;
  letter-spacing: 0.1em;
  z-index: 1;
  color: #1C1B19;
}

.pagination__buttons a {
  background: #A89E8C;
  color: #2C2A28;
  transition: background 0.2s, color 0.2s;
}

.pagination__buttons a:hover {
  background: #000000;
  color: #E5E3DE;
}

/* ==== BUTTONS ==== */
.button {
  background: #A89E8C;
  color: #2C2A28;
  font-size: unquote("clamp(0.7rem, 0.9vw, 0.9rem)");
  padding: unquote("clamp(0.35rem, 0.8vw, 0.65rem)") unquote("clamp(0.65rem, 1.2vw, 1rem)");
  border-radius: 6px;
  transition: background 0.2s, color 0.2s;
}

.button:hover {
  background: #000000;
  color: #E5E3DE;
}

/* ==== RESPONSIVE BREAKPOINTS ==== */
@media (max-width: 1024px) {
  .post-content pre {
    font-size: 0.9rem;
  }
}

@media (max-width: 768px) {
  .post {
    background-size: 200px;
    border-radius: 0px;
  }

  .post-title {
    font-size: unquote("clamp(1.8rem, 5vw, 2.2rem)");
  }

  .post-content {
    margin-top: 1.5rem;
  }

  .post-content pre {
    padding: 0.7rem;
  }

  .button {
    font-size: 0.85rem;
  }

  .post-info {
    font-size: 0.5rem;
  }

  .post-info svg {
    width: 20px;
  }
}

@media (max-width: 480px) {
  .post {
    padding: 1rem;
    background-size: 150px;
  }

  .post-title {
    font-size: 1.7rem;
  }

  .post-content h3 {
    font-size: 1.4rem;
  }

  .post-content p,
  .post-content p code {
    font-size: 0.95rem;
  }

  .post-content pre {
    font-size: 0.85rem;
    padding: 0.45rem;
  }

  .pagination__buttons a {
    font-size: 0.75rem;
    padding: 0.5rem 0.7rem;
  }
}

@media (max-width: 360px) {
  .post {
    padding: 0.75rem;
  }

  .post-title {
    font-size: 1.4rem;
  }

  .post-content p {
    font-size: 0.85rem;
  }
}
