html {
  box-sizing: border-box;
  line-height: 1.6;
  letter-spacing: 0.06em;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  // font-family: "JetBrains Mono", monospace;
  font-family: 'VT323', monospace;
  // font-family: "Fira Code", "Fira Mono", Menlo, Consolas, "DejaVu Sans Mono", monospace;
  // font-family: "Inter UI", -apple-system, BlinkMacSystemFont, "Roboto", "Segoe UI", Helvetica, Arial, sans-serif;
  font-display: auto;
  font-size: 1rem;
  line-height: 1.54;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;

  display: flex;
  flex-direction: column;

  // Default
  background-color: $dark-background;
  color: $dark-color;

  background-image: linear-gradient(
      0deg,
      rgba(240, 248, 255, 0.03) 1px,
      transparent 1px
    ),
    linear-gradient(90deg, rgba(240, 248, 255, 0.03) 1px, transparent 1px),
    linear-gradient(0deg, rgba(240, 248, 255, 0.06) 1px, transparent 1px),
    linear-gradient(90deg, rgba(240, 248, 255, 0.06) 1px, transparent 1px);
  background-size: 10px 10px, 10px 10px, 40px 40px, 40px 40px;

  @media #{$media-size-phone} {
    font-size: 1rem;
  }
}

::selection {
  background-color: #A89E8C; /* warm soft accent from post */
  color: #1C1B19;             /* warm black text for readability */
}

/* For Firefox */
::-moz-selection {
  background-color: #A89E8C;
  color: #1C1B19;
}



h2,
h3,
h4,
h5,
h6 {
  display: flex;
  align-items: center;
  line-height: 1.3;
}

h1 {
  font-size: 2.625rem;
}

h2 {
  font-size: 1.625rem;
  margin-top: 2.5em;
}

h3 {
  font-size: 1.375rem;
}

h4 {
  font-size: 1.125rem;
}

@media #{$media-size-phone} {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.4rem;
  }

  h3 {
    font-size: 1.15rem;
  }

  h4 {
    font-size: 1.125rem;
  }
}

a {
  color: inherit;
}

.post-content a {
  color: lightcoral;
    text-decoration: underline;
  text-underline-offset: 2px;
  transition: color 0.2s ease, text-decoration-color 0.2s ease;
  // color: #F0EDE5;
}

.post-content a:hover {
  color: #B04A3F;
  text-decoration-color: #D65A4F;
}


.post-info p{
  font-family: 'VT323', monospace;

}

img {
  display: block;
  max-width: 100%;

  &.left {
    margin-right: auto;
  }

  &.center {
    margin-left: auto;
    margin-right: auto;
  }

  &.right {
    margin-left: auto;
  }
  &.circle {
    border-radius: 50%;
    max-width: 25%;
    margin: auto;
  }
}

figure {
  display: table;
  max-width: 100%;
  margin: 25px 0;

  &.left {
    margin-right: auto;
  }

  &.left-floated {
    margin-right: auto;
    float: left;
    img {
      margin: 20px 20px 20px 0;
    }
  }

  &.center {
    margin-left: auto;
    margin-right: auto;
  }

  &.right {
    margin-left: auto;
  }

  &.right-floated {
    margin-left: auto;
    float: right;
    img {
      margin: 20px 0 20px 20px;
    }
  }

  &.rounded {
    img {
      border-radius: 50%;
    }
  }

  figcaption {
    font-size: 14px;
    margin-top: 5px;
    opacity: 0.8;

    &.left {
      text-align: left;
    }

    &.center {
      text-align: center;
    }

    &.right {
      text-align: right;
    }
  }
}

em, i, strong {
  // Default
  color: $dark-color-variant;

  @media (prefers-color-scheme: dark) {
    color: $dark-color-variant;
  }

  @media (prefers-color-scheme: light) {
    color: $dark-color-variant;
  }

  [data-theme=dark] & {
    color: white;
  }

  [data-theme=light] & {
    color: black;
  }
}

code {
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  font-display: auto;
  font-feature-settings: normal;
  padding: 1px 6px;
  margin: 0 2px;
  border-radius: 5px;
  font-size: 0.95rem;
  // Default
  background: #3a3a3a;
  // color: #CBB893;
}

pre {
  [data-theme=dark] & {
    background-color: $dark-background-secondary;
  }

  [data-theme=light] & {
    background-color: $dark-background-secondary;
  }

  padding: 10px 10px 10px 20px;
  border-radius: 8px;
  font-size: 0.95rem;
  overflow: auto;

  @media #{$media-size-phone} {
    white-space: pre-wrap;
    word-wrap: break-word;
  }

  code {
    background: none !important;
    margin: 0;
    padding: 0;
    font-size: inherit;
    // Default
    color: #ccc;

    @media (prefers-color-scheme: dark) {
      color: inherit;
    }
  
    @media (prefers-color-scheme: light) {
      color: #ccc;
    }
  
    [data-theme=dark] & {
      color: inherit;
    }
  
    [data-theme=light] & {
      color: #ccc;
    }
  }
}

blockquote {
  border-left: 3px solid #3eb0ef;
  margin: 40px;
  padding: 10px 20px;

  @media #{$media-size-phone} {
    margin: 10px;
    padding: 10px;
  }

  &:before {
    content: "”";
    font-family: Georgia, serif;
    font-display: auto;
    font-size: 3.875rem;
    position: absolute;
    left: -40px;
    top: -20px;
  }

  p:first-of-type {
    margin-top: 0;
  }

  p:last-of-type {
    margin-bottom: 0;
  }
}

ul,
ol {
  margin-left: 40px;
  padding: 0;

  @media #{$media-size-phone} {
    margin-left: 20px;
  }
}

ol ol {
  list-style-type: lower-alpha;
}

.container {
  flex: 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  // text-align: center;
}

.content {
  display: flex;
  flex-direction: column;
  flex: 1 auto;
  align-items: center;
  justify-content: center;
  margin: 0;

  @media #{$media-size-phone} {
    margin-top: 0;
  }
}

.comments {
  margin: 0 auto;
  min-width: 40%;
}

@media (max-width: 784px) {
  .comments {
    min-width: 90%;
  }
}

@media (max-width: 900px) {
  .comments {
    min-width: 80%;
  }
}


hr {
  width: 100%;
  border: none;
  height: 1px;
  // Default
  background: $dark-border-color;

  @media (prefers-color-scheme: dark) {
    background: $dark-border-color;
  }

  @media (prefers-color-scheme: light) {
    background: $dark-border-color;
  }

  [data-theme=dark] & {
    background: $dark-border-color;
  }

  [data-theme=light] & {
    background: $dark-border-color;
  }
}

.hidden {
  display: none;
}

.hide-on-phone {
  @media #{$media-size-phone} {
    display: none;
  }
}

.hide-on-tablet {
  @media #{$media-size-tablet} {
    display: none;
  }
}

// Accessibility
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  width: auto;
  top: 5px;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  z-index: 100000;
}

.background-image {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
}

// Prism JS Additionals
.highlight {
  margin: 30px auto;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;

}

.project {
  border-radius: 10px;
  padding: 1rem;
  background: rgba(158, 151, 125, 1);
  font-family: VT323, monospace;
  position: relative;
 color: #3E3B38;


  animation: float 3s ease-in-out infinite;
}


@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.p-title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0;
  font-size: 1.6rem;
  color: #4F2A18;
}

.project p {
  font-size: 1.4rem; line-height: 1.5; font-weight: normal;
}

.project h3 {
  margin-top: 0;
  font-size: 1.7rem;
  font-family: VT323, monospace;
}

.bullets li::marker {
  content: ">> ";
  font-size: 13px;
  color: #8F5C36;
  margin-left: -37px;
  margin-right: 21px;
  line-height: 16px;
}

.bullets p {
  margin: 0;
}

.right {
  text-align: right;
  bottom: 10px;
  right: 10px;
  position: absolute
}

.right a {
  color: #4F2A18;
}

.elevated-image {
  display: inline-block;
  width: 100%; /* adjust as needed */
  height: auto;
  position: relative;


  /* elevate using shadow */
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5),
              0 10px 20px rgba(0, 0, 0, 0.3);

  /* optional subtle perspective tilt */
  transform: translateY(-5px) scale(1.02);

  /* smooth transition for hover */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* optional: hover effect */
.elevated-image:hover {
  transform: translateY(-10px) scale(1.04);
  box-shadow: 0 30px 60px rgba(0,0,0,0.6),
              0 15px 30px rgba(0,0,0,0.4);
}


